import React from 'react';
import Vehicle from '../assets/images/delivery_truck.jpg';
import PhoneEnabledIcon from '@mui/icons-material/PhoneEnabled';

function HomeDelivery() {
    return (
        <div className="filtering-system py-5 text-white">
            <div className="container-xl pt-5">
                <div className="row align-items-center">
                    <div className="col-md-6 mb-5 p-4">
                        <img src={Vehicle} alt="Vehicles" className='img-fluid rounded-4 box-shadow-lg' />
                    </div>
                    <div className="col-md-6 mb-5 px-4 py-5">
                        <h1 className="section-heading mb-4">Complimentary Delivery Service.</h1>
                        <p className='fs-20 mb-4'>Our delivery services with-in Karachi city limits are free. To arrange delivery to multiple locations contact:</p>
                        <div className="mb-4">
                            <a href="tel:+92336-7719535" className='contact-btn rounded-pill d-inline-block'><PhoneEnabledIcon className='me-2' /> 0336-7719535</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default HomeDelivery;